  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding: 1rem;
  padding-top: 0; }

.task {
  background: white;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.322);
  display: flex;
  flex-direction: column;
  border-radius: .2rem; }
  .task .top {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr .3fr;
    align-items: center;
    padding: .5rem;
    padding-inline: 1rem;
    color: #3a3434; }
    .task .top .title {
      font-weight: 600;
      text-transform: uppercase; }
    .task .top .date {
      opacity: .7; }
    .task .top .priority {
      width: fit-content;
      font-weight: 600;
      opacity: .5;
      text-transform: uppercase; }
    .task .top .open-close {
      display: flex;
      justify-content: end;
      cursor: pointer; }
  .task .hidden {
    display: none;
    flex-direction: column;
    gap: 1rem;
    padding: 1rem; }
    .task .hidden .delete-task {
      padding: .5rem;
      font-weight: 600; }
    .task .hidden .check-lists {
      display: flex;
      flex-direction: column;
      gap: .5rem; }
      .task .hidden .check-lists .list {
        display: flex;
        align-items: center;
        background-color: #dededf;
        border-radius: .2rem;
        padding: .5rem; }
        .task .hidden .check-lists .list div {
          flex-grow: 1; }
    .task .hidden .note {
      border-radius: .2rem;
      background-color: #9bf3d6;
      min-height: 4rem;
      padding: .5rem;
      font-size: .8rem; }

.hidden.active {
  display: flex; }
