  position: absolute;
  inset: 50% 0 0 0;
  z-index: 3;
  display: grid;
  width: auto;
  grid-template-rows: min-content;
  background-color: aliceblue; }
  .box .box-header {
    display: flex;
    align-items: center;
    justify-content: space-around;
    height: 4rem;
    font-weight: 600;
    font-size: 1.2rem;
    background: #c4c4c4; }
  .box .box[active] {
    background-color: aquamarine; }
  .box .box-menu {
    background-color: #dbdbdb;
    overflow-y: auto; }

.close-box {
  position: absolute;
  top: .7rem;
  left: .5rem;
  cursor: pointer; }

@media screen and (max-width: 1000px) {
  .box {
    position: fixed; } }
